import React from 'react';
import './App.css';
import Main from './Main.js';

import {
	Link
} from 'react-router-dom';

class Button extends React.Component {
	constructor(props)
	{
		super(props);
		this.state =
		{
			Num : this.props.ButtonNum,
			// Active: true
		};
	}

	render()
	{
		let Class
		(this.props.value===1) ? Class = "ButtonActive" : Class = "Button";
		return (
			<div>
			<Link to={this.props.ButtonName}
			className = {Class}
			onClick={() => this.props.onClick()} > {this.props.ButtonName}
			</Link>
			</div>
			)
		}
	}


class Body extends React.Component {

	constructor(props)
	{
		super(props);
		this.state =
		{
			ActiveTab: 0,
			Tabs: ["Home", "Services", "Clients", "Contact", "About"]
		};
	}

	renderPageTop(){
		return (
			<header className="App-header">
			logo here
			</header>
			)
		}

		renderPageFooter()
		{
			return (
				<footer className="footer">
				SIA TeknoLogic &copy; 2020
				</footer>
				)
			}

			handleClick(ButtonNum) {
				this.setState({ActiveTab: ButtonNum});
			}

			renderButton(num) {
				let isActive
				isActive = 0;
				(this.state.ActiveTab===num) ? isActive = 1 : isActive = 0;

				return (
					<Button
					value={isActive}
					ButtonName={this.state.Tabs[num]}
					ButtonNum={num}
					onClick={() => this.handleClick(num)}
					/>
					);
				}

				render() {
					return (
						<div>
						{this.renderPageTop()}
						<div className="topnav">
						{this.renderButton(0)}{this.renderButton(1)}{this.renderButton(2)}{this.renderButton(3)}{this.renderButton(4)}
						</div>
						{this.renderPageFooter()}
						</div>
						);
					}
				}



function App() {
	return (
		<div>
		<Body />
		<Main />
		</div>
		);
	}

	export default App;
