import React from 'react';
import './Home.css';

import {
    WelcomeBlockTitle,
    RnDP,
    DesignHouseP,
    EnthusiastsP,
    SignalP,
    ExpertiseBlockTitle,
    HWP,
    EEP,
    IoTP,
    MLP
}  from "./HomeContent.js"

import IntroImg from "./undraw_alien_science_nonm.svg"
import DesignHouseImg from "./undraw_product_iteration_kjok.svg"
import RnDPImg from "./undraw_prototyping_process_rswj.svg"
import EnthusiastsImg from "./undraw_researching_22gp.svg"
import DspImg from './undraw_signal_searching_bhpc.svg'
import DsImg from './undraw_data_xmfy.svg'
import HWImg from './hardware.svg'
import EEImg from './undraw_circuit_board_4c4d.svg'
import IoTImg from './AI.svg'

import ExpertiseTitleImg from './undraw_experience_design_eq3j.svg'


function renderTopicText (props)
    {
        return (
            <div className="text">{props.content}</div>
            )
    }

function renderTopicArt(props)
    {
        return (<div className="art"><img src={props.icon} alt={props.alt} /></div>)
    }

function  RenderTopic(props)
    {
        return (
        <div className="topic">
            {renderTopicText(props.topic)}
            {renderTopicArt(props.topic)}
        </div> );
    }
function RenderBlockHeader(props)
{
    return (
        <div className="header">
            <div className="title">{props.title}</div>
            <div className="art"><img src={props.icon} alt={props.alt} /></div>
        </div>
    )
}


function RenderBlock(props)
{
    if (props.block.class==="block")
    {
        return (
            <div className={props.block.class}>
                    {RenderBlockHeader(props.block)}
                    {props.block.contents.map(newTopic => {
                        return (
                                  <RenderTopic key={newTopic.id} topic ={newTopic} />
                        )
                        })}

                    </div>);
    }
    else if (props.block.class==="block small")
    {
        return (
            <div className={props.block.class}>
                    {RenderBlockHeader(props.block)}
                    <div className="topics">
                        {props.block.contents.map(newTopic => {
                            return (
                                    <RenderTopic key={newTopic.id} topic ={newTopic} />
                            )
                            })}
                        </div>
                    </div>);
    }


}
class Home extends React.Component {
    constructor(props)
    {
        super(props);


        console.log('Running Home Constructor\n')
        let welcomeTopics = [
            {id:1, title:"DesignHouse", content:DesignHouseP, icon:DesignHouseImg, alt:"Design House"},
            {id:2, title:"RnD", content:RnDP, icon:RnDPImg, alt:"RND house"},
            {id:3, title:"Attitude", content:EnthusiastsP, icon:EnthusiastsImg, alt:"Attitude"}
        ];

        let expertiseTopics = [
            {id:1, title:"DSP", content:SignalP, icon:DspImg, alt:"Digital Signal Processing"},
            {id:2, title:"DSP", content:EEP, icon:EEImg, alt:"Digital Signal Processing"},
            {id:3, title:"DSP", content:HWP, icon:HWImg, alt:"Digital Signal Processing"},
            {id:4, title:"DSP", content:IoTP, icon:IoTImg, alt:"Digital Signal Processing"},
            {id:5, title:"DSP", content:MLP, icon:DsImg, alt:"Digital Signal Processing"}
        ]

        let blocks = [
            {id:1, title:WelcomeBlockTitle, contents: welcomeTopics, icon:IntroImg, alt:"We Are:", class: "block"},
            {id:2, title:ExpertiseBlockTitle, contents: expertiseTopics, icon:ExpertiseTitleImg, alt:"Our Expertise", class: "block small"}
        ];

        this.state =
        {
            blocks:blocks
        };
    }


    render()
    {
        console.log('Rendering Home Class\n')
        return (
        <div className="home">
                {this.state.blocks.map(newBlock => {
                    return (
                              <RenderBlock key={newBlock.id} block={newBlock} />
                    )
                    })}
        </div>);
    }
} export default Home;


